import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./source-of-enquiry-add.component.html",
	encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})
export class SourceOfEnquiryAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;
	
soeForm: FormGroup;
static SerialnoMandatory ="0";
bootstrapFromValues = [];
soeId = 0;
ajaxData : SourceOfEnquiryData[];
sourceofenquiry= new SourceOfEnquiryData();  
config = new configDetails(); 
status:string;    

public title=[];
	
msg = [];
	 

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.soeId).subscribe(params=>{
			if(params.soeId != undefined){
				this.soeId=params.soeId; 
			}
		}); 
//	this.getConfigDetails();
	if(this.soeId == 0){
		this.status="Add";
	}else{
		this.populateData();
		this.status="Update";
	}
}
	
    ngOnInit() {
		this.soeForm = new FormGroup({			
			'soeId' : new FormControl(''),
			'soeName' : new FormControl('', [ Validators.required]),
			'soeEntryId' : new FormControl(''),
			'soeEntryDate' : new FormControl(''),
			'soeModifiedId' : new FormControl(''),
			'soeModifiedDate' : new FormControl(''),
			
		});
		}
	  
	    get soeName(){ return this.soeForm.get('soeName')};
	    get soeEntryId(){ return this.soeForm.get('soeEntryId')};
	    get soeEntryDate(){ return this.soeForm.get('soeEntryDate')};
	    get soeModifiedId(){ return this.soeForm.get('soeModifiedId')};
	    get soeModifiedDate(){ return this.soeForm.get('soeModifiedDate')};
	 
 
	  
	   
	  populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<SourceOfEnquiryData>(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/soe-data',
							 			JSON.stringify({ soeId :this.soeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.sourceofenquiry = data['populateData'][0];
																		console.log(this.sourceofenquiry);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }

	ngAfterViewInit(){
		   $.getScript('assets/demo/default/custom/components/forms/widgets/select2.js',function(){
		   });
	}	
	
		 toggleAnimation(){
			this._router.navigate(['portal/source-of-enquiry-list']);
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.sourceofenquiry[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.soeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.soeId == 0){ 
				this.sourceofenquiry['requestType'] = 'add';
			}else{
				this.sourceofenquiry['requestType'] = 'update';
				this.sourceofenquiry['soeId'] = this.soeId+'';
			}
		 	this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/soe',
									JSON.stringify(this.sourceofenquiry),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.soeForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.sourceofenquiry);
	  }

 onDelete(){
		 this.sourceofenquiry['requestType'] = 'delete';
		this.sourceofenquiry['soeId'] = this.soeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/soe',
									JSON.stringify(this.sourceofenquiry),
						{ headers, withCredentials: true }).subscribe(data => {
			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.soeForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	
	  	
}

export class SourceOfEnquiryData {
	
	soeId: string ;
	soeName: string;
	soeEntryId:string;
	soeEntryBy:string;
	soeEntryDate:string;
    soeModifiedId:string;
	soeModifiedBy:string;
    soeModifiedDate:string;
	
	 
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}



